<template>
    <div>
        <div>
      
            <!-- 二级路由插座 -->
            <router-view></router-view>

        </div>
        <van-tabbar v-model="active" @change='togglemenu'>
            <van-tabbar-item v-for="(item,index) in mainData" :key="index" :icon="item.icon">{{item.title}}</van-tabbar-item>  
        </van-tabbar>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                active: 0,
                mainData:[
                    {
                        title:"书架",
                        icon:"column",
                        torouter:"Bookrack",
                         urls:"/main/bookrack"
                    },
                    {
                        title:"书城",
                        icon:"bookmark",
                        torouter:"Find",
                         urls:"/main/find"
                    },
                    
                    {
                        title:"我的",
                        icon:"manager",
                        torouter:"My",
                         urls:"/main/my"
                    },
                ]
            }
        },
        created(){
            let hash = location.hash.slice(1);
            
            for(let i = 0 ; i < this.mainData.length ; i ++){
                if(this.mainData[i].urls == hash){
                    this.active = i
                    break
                }
            }
        },
        methods: {
            togglemenu(index){
                
                this.$router.push({name:this.mainData[index].torouter})
            }
        },
        
    }
</script>

<style lang="less" scoped>
/deep/.van-tabbar-item--active{
    color: #05d390;
}
</style>